@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  font-family: "Righteous", cursive;
  background-image: url(/speedgame/static/media/14031728.709d10e8.jpg);
  background-attachment: fixed;
  text-align: center;
}

button {
  font-family: inherit;
  padding: 1rem;
  margin: 1rem;
  background: white;
  box-shadow: 0 0 5px #747474;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  text-transform: uppercase;
}

.circle {
  border-radius: 50px;
  height: 100px;
  width: 100px;
  background-color: grey;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 1rem;
}

circle:hover {
  font-size: 1.1rem;
  border: solid 4px rgb(48, 48, 48);
  font-weight: bold;
}

.green {
  background-color: green;
}

.yellow {
  background-color: yellow;
}

.orange {
  background-color: orange;
}

.red {
  background-color: red;
}

.active {
  background-image: url(/speedgame/static/media/flame.2a7fa768.png);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
}

.gameover_box {
  background: white;
  padding: 1rem;
  width: 40%;
  min-height: 25%;
  border-radius: 5px;
  position: relative;
  display: grid;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
}

.gameover_box button {
  background: lightgrey;
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}

.circles {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

